import React from 'react'
import { NavBar, Icon } from 'antd-mobile'
// import no_message from 'assets/no_message.png'
import bind_logo from 'assets/bind_logo.png'
function BindMessage() {
  const onLeftClick = () => {
    window.history.back(-1)
  }
  return (
    <div className="bindMessage">
      <NavBar
        mode="light"
        icon={<Icon type="left" />}
        onLeftClick={onLeftClick}
        className="nav_bar"
      >
        绑定学生信息
      </NavBar>
      {/* <div className="no_bind">
        <img src={no_message} alt=""></img>
        <span>当前尚未绑定学生信息</span>
      </div> */}
      <div className="has_bind">
        <div className="bind_left">
          <img src={bind_logo} alt=""></img>
        </div>
        <div className="bind_right">
          <span className="name">小明</span>
          <span className="bind_class">七（三）班</span>
          <span className="number">学号： 10254244</span>
        </div>
      </div>
    </div>
  )
}

export default BindMessage
