import React from 'react'
import './App.scss'
import Layout from './pages/home/Layout.js'
// import CheckReports from './pages/home/checkReports/CheckReports'

import { HashRouter as Router, Route, Switch } from 'react-router-dom'

// import CheckReports from './pages/home/checkReports/CheckReports'
import BindMessage from './pages/center/BindMessage'
import Feedback from './pages/center/Feedback'

import CheckReports from './pages/home/checkReports/CheckReports'
import ReportDetail from './pages/home/checkReports/ReportDetail'
import HealthTrend from './pages/home/checkReports/HealthTrend'
import HealthIndex from './pages/home/checkReports/HealthIndex'
import Login from './pages/login/login'
function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route exact path="/" component={Layout} />
          <Route exact path="/CheckReports" component={CheckReports} />
          <Route exact path="/HealthTrend" component={HealthTrend} />
          <Route exact path="/HealthIndex" component={HealthIndex} />
          <Route exact path="/ReportDetail" component={ReportDetail} />
          <Route exact path="/BindMessage" component={BindMessage} />
          <Route exact path="/feedback" component={Feedback} />
          <Route exact path="/Login" component={Login} />
        </Switch>
      </Router>
    </div>
  )
}

export default App
