import React, { useState } from 'react'
import './login.scss'
import { isMobileNumber } from '../../utils/Validate'
import { Toast } from 'antd-mobile'
import delete_icon from 'assets/delete.png'
import { sendMessage } from '../../api/Login'

function showToast() {
  Toast.info('请输入正确的手机号', 1)
}

function Login() {
  const [value, setValue] = useState()
  // 获取验证码
  const handleChange = (e) => {
    setValue(e.target.value)
  }
  const handleClick = async () => {
    // 先校验手机号
    if (!isMobileNumber(value)) {
      showToast()
      return false
    }

    const params = {
      name: '17520465320',
    }

    let res = await sendMessage(params)
    console.log(res)
  }

  const handleBack = ()=>{
    
  }
  return (
    <div className="login">
      <div className="login_delete" onClick={handleBack}>
        <img src={delete_icon} alt=""></img>
      </div>
      <div className="login_header">
        <h1>登录泓贵堂</h1>
        <span>登录即同意“用户协议”和“隐私政策”</span>
      </div>
      <div className="login_content">
        <form>
          <input
            type="text"
            placeholder="手机号"
            value={value}
            onChange={handleChange}
          ></input>
          <span>未注册手机号验证通过后自动注册</span>
          <div className="get_code">
            <input type="text" placeholder="验证码"></input>
            <span onClick={handleClick}>获取验证码</span>
          </div>
        </form>
      </div>
      <div className="login_btn">
        <button>登录</button>
      </div>
    </div>
  )
}

export default Login
