import React from 'react'
import { NavBar, Icon } from 'antd-mobile'
function ReportDetail() {
  const onLeftClick = () => {
    window.history.back(-1)
  }
  return (
    <div className="reportDetail">
      <NavBar
        mode="light"
        icon={<Icon type="left" />}
        onLeftClick={onLeftClick}
      >
        查看报告
      </NavBar>
      <div className="grid">
        <div>
          <span className="top">姓名：</span>
          <span className="bottom">刘小明</span>
        </div>
        <div>
          <span className="top">身高：</span>
          <span className="bottom">130CM</span>
        </div>
        <div>
          <span className="top">体重：</span>
          <span className="bottom">30kg</span>
        </div>
        <div>
          <span className="top">体脂率：</span>
          <span className="bottom">25.6%</span>
        </div>
      </div>
      <div className="describe">
        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean euismod
        bibendum laoreet. Proin gravida dolor sit amet lacus accumsan et viverra
        justo commodo. Proin sodales pulvinar sic tempor. Sociis natoque
        penatibus et magnis dis parturient montes, nascetur ridiculus mus. Nam
        fermentum, nulla luctus pharetra vulputate, felis tellus mollis orci,
        sed rhoncus pronin sapien nunc accuan eget.
      </div>
    </div>
  )
}

export default ReportDetail
