import React from 'react'
import { TabBar } from 'antd-mobile'
import center from 'assets/center.png'
import centerActive from 'assets/center_active.png'
import home from 'assets/home.png'
import homeActive from 'assets/home_active.png'

import MainHome from './mainHome/MainHome.js'
import PersonalCenter from '../center/PersonalCenter'

class Layout extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      selectedTab: 'MainHome',
      hidden: false,
      fullScreen: true,
    }
  }

  render() {
    return (
      <div
        style={
          this.state.fullScreen
            ? { position: 'fixed', height: '100%', width: '100%', top: 0 }
            : { height: 400 }
        }
      >
        <TabBar
          unselectedTintColor="#949494"
          tintColor="#33A3F4"
          barTintColor="white"
          hidden={this.state.hidden}
        >
          <TabBar.Item
            title="首页"
            key="mainHome"
            icon={
              <div
                style={{
                  width: '22px',
                  height: '22px',
                  background: `url(${home}) center center /  21px 21px no-repeat`,
                }}
              />
            }
            selectedIcon={
              <div
                style={{
                  width: '22px',
                  height: '22px',
                  background: `url(${homeActive}) center center /  21px 21px no-repeat`,
                }}
              />
            }
            selected={this.state.selectedTab === 'mainHome'}
            onPress={() => {
              this.setState({
                selectedTab: 'mainHome',
              })
            }}
          >
            <MainHome></MainHome>
          </TabBar.Item>

          <TabBar.Item
            icon={
              <div
                style={{
                  width: '22px',
                  height: '22px',
                  background: `url(${center}) center center /  21px 21px no-repeat`,
                }}
              />
            }
            selectedIcon={
              <div
                style={{
                  width: '22px',
                  height: '22px',
                  background: `url(${centerActive}) center center /  21px 21px no-repeat`,
                }}
              />
            }
            title="我的"
            key="PersonalCenter"
            selected={this.state.selectedTab === 'PersonalCenter'}
            onPress={() => {
              this.setState({
                selectedTab: 'PersonalCenter',
              })
            }}
          >
            <PersonalCenter></PersonalCenter>
          </TabBar.Item>
        </TabBar>
      </div>
    )
  }
}

export default Layout
