import React from 'react'
import './center.scss'
import { Link } from 'react-router-dom'
import logo from 'assets/logo.png'
import bindMessage from 'assets/bind_message.png'
import feedback from 'assets/feedback.png'
import quit from 'assets/quit.png'
function PersonalCenter() {
  return (
    <div>
      <div className="personal_header">
        <img src={logo} alt=""></img>
        <span>13897845207</span>
      </div>
      <div className="person_options">
        <Link to="/BindMessage">
          <div className="bind_message">
            <div className="top">
              <img src={bindMessage} alt=""></img>
              <div className="textword">绑定学生信息</div>
            </div>
            <div>&gt;</div>
          </div>
        </Link>
        <Link to="/feedback">
          <div className="feedback">
            <div className="top">
              <img src={feedback} alt=""></img>
              <div className="textword">意见反馈</div>
            </div>

            <div>
              <span>&gt;</span>
            </div>
          </div>
        </Link>
        <Link to="/quit">
          <div className="quit">
            <div className="top">
              <img src={quit} alt=""></img>
              <div className="textword">退出当前帐号</div>
            </div>
            <div>&gt;</div>
          </div>
        </Link>
      </div>
    </div>
  )
}

export default PersonalCenter
