import React, { useEffect } from 'react'
import { NavBar, Icon } from 'antd-mobile'
import './checkReports.scss'
// 引入 ECharts 主模块
import echarts from 'echarts'
import 'echarts/lib/chart/line'

function HealthIndex() {
  const onLeftClick = () => {
    window.history.back(-1)
  }

  useEffect(() => {
    let myChart = echarts.init(document.getElementById('health_index'))
    let option = {
      tooltip: {},

      radar: {
        name: {
          textStyle: {
            color: '#fff',
            backgroundColor: '#999',
            borderRadius: 3,
            padding: [3, 5],
          },
        },
        indicator: [
          { name: '销售', max: 6500 },
          { name: '管理', max: 16000 },
          { name: '信息技术', max: 30000 },
          { name: '客服', max: 38000 },
          { name: '研发', max: 52000 },
        ],
      },
      series: [
        {
          name: '预算 vs 开销',
          type: 'radar',
          // areaStyle: {normal: {}},
          data: [
            {
              value: [4300, 10000, 28000, 35000, 50000, 19000],
              name: '预算分配',
            },
          ],
        },
      ],
    }
    myChart.setOption(option)
  })

  return (
    <div className="health_content">
      <NavBar
        mode="light"
        icon={<Icon type="left" />}
        onLeftClick={onLeftClick}
        className="nav_bar"
      >
        健康趋势
      </NavBar>
      <div id="health_index" className="health_index"></div>
    </div>
  )
}

export default HealthIndex
