import React, { useEffect } from 'react'
import { NavBar, Icon } from 'antd-mobile'
import './checkReports.scss'
// 引入 ECharts 主模块
import echarts from 'echarts'
import 'echarts/lib/chart/line'
function HealthTrend() {
  const onLeftClick = () => {
    window.history.back(-1)
  }

  useEffect(() => {
    let myChart = echarts.init(document.getElementById('health_trend'))
    myChart.setOption({
      title: {
        text: '健康综合得分',
        left: 'left',
        show: true,
      },
      xAxis: {
        type: 'category',
        data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
      },
      yAxis: {
        type: 'value',
      },
      series: [
        {
          data: [0, 18, 16, 20, 30],
          type: 'line',
          smooth: true,
        },
      ],
    })
  })
  return (
    <div className="health_content">
      <NavBar
        mode="light"
        icon={<Icon type="left" />}
        onLeftClick={onLeftClick}
        className="nav_bar"
      >
        健康趋势
      </NavBar>
      <div id="health_trend" className="trend"></div>
    </div>
  )
}

export default HealthTrend
