import React from 'react'
import report from 'assets/report.png'
import trend from 'assets/trend.png'
import { Link } from 'react-router-dom'
function MainButton() {
  return (
    <div>
      <Link to="/CheckReports">
        <div className="buttonStyle topButton">
          <img className="picStyle" src={report} alt=""></img>
          <span>查看报告</span>
        </div>
      </Link>
      <Link to="/HealthTrend">
        <div className="buttonStyle">
          <img className="picStyle" src={trend} alt=""></img>
          <span>健康趋势</span>
        </div>
      </Link>
    </div>
  )
}

export default MainButton
