import React from 'react'
import { useHistory } from 'react-router-dom'
import { Progress } from 'antd'

import './mainHomeStyle.scss'
function Score() {
  let history = useHistory()
  const handleToHealthIndex = () => {
    history.push('/HealthIndex')
  }

  return (
    <div className="myStyle" onClick={handleToHealthIndex}>
      <Progress
        strokeLinecap="square"
        type="dashboard"
        percent={75}
        gapDegree={90}
        width={230}
        strokeWidth={5}
        strokeColor={'#FFE336'}
        showInfo={true}
        format={(percent) => `${percent}`}
      />
      <div className="CoreText">
        <span>健康综合得分</span>
      </div>
    </div>
  )
}

export default Score
